import { createWebHistory, createRouter } from "vue-router";


import Home from '../components/homeMsg.vue';
import List from '../components/blogList.vue';
import view404 from '../components/view404.vue';
import Detail from '../components/blogDetail.vue';
import Author from '../components/blogAuthor.vue';
import Comment from '../components/blogComment.vue';
import Hello from '../components/HelloWorld.vue'


const routes = [
  {
    path: '/detail/:id',
    component: Detail,
    children: [ //상대경로 주의 "/"
      { 
        path:"author",
        component: Author,
      },
      {
        path:"comment",
        component: Comment,
      }
    ]
  },
  {
    path: '/list',
    component: List,
    children: [
      {
        path:"detail/:id",
        component: Detail,
      }
    ],
    // beforeEnter: (to, from)=>{
    //   return to.fullPath
    // }

  },
  {
    path: '/',
    component: Home,
  },
  {
    path: '/:anything',
    component: view404,
  },
  {
    path:"/hello",
    component: Hello,
    // beforeEnter: ()=>{
    //   if (로그인했냐 == false) {
    //     return '/login'
    //   }
    // }
  }
//   {
//     path: '/경로',
//     component: 위에서 import 해온 컴포넌트
//   }
];

const router = createRouter({
  //history: createWebHashHistory()
  //이렇게 설정해놓는건데 이걸 Hash mode라고 합니다.
  //이걸 선택하시면 URL에 전부 #이 붙은 채로 시작합니다.
  //codingapple.com/#/detail 이런 식입니다
  history: createWebHistory(),
  routes,
});

export default router; 
